
@import '~common/fonts/iconfont.css';
.carousel .vux-slider > .vux-indicator.custom-bottom {
  bottom: 0.186666666666667rem;
}
.carousel .vux-slider > .vux-swiper > .vux-swiper-item > a > .vux-swiper-desc {
  background-image: none;
}
.carousel .vux-slider > .vux-indicator > a > .vux-icon-dot,
.carousel .vux-slider .vux-indicator-right > a > .vux-icon-dot {
  background-color: #fff;
  border-radius: 0.026666666666667rem;
  width: 0.266666666666667rem;
  height: 0.053333333333333rem;
}
.carousel .vux-slider > .vux-indicator > a > .vux-icon-dot.active,
.carousel .vux-slider .vux-indicator-right > a > .vux-icon-dot.active {
  background-color: #444;
}
.carousel .vux-slider > .vux-swiper > .vux-swiper-item > a > .vux-img {
  background-size: 100% 100%;
}


